body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.calcParent{
  position: relative;
  border:1px solid grey;
  margin:auto;
  max-width:500px;
  text-align: center;
}

.keyPadParent{
  position:relative;
  border:0px solid red;
  max-width:500px;
  height:auto;
}

.keyPadParent button{
  position: relative;
  color: black;
  font-size:20px;
  width: 25%;
  height: 50px;
}

.resultParent{
  width: auto;
  height: 80px;
  background:#b1adad;
}

.resultParent p{
    text-align: right;
    font-size: 4vmin;
    position: absolute;
    right: 0;
}
